___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --primary-color: rgb(17, 86, 102);
    --primary-color-darker: rgb(9, 48, 56);
    --sucesso: rgb(109, 255, 182);
    --falha: rgb(241, 25, 18);
}

* {
    box-sizing: border-box;
    outline: 0;
}

body {
    margin: 0;
    padding: 0;
    background: var(--primary-color);
    font-family: 'Mooli', sans-serif;
    font-size: 1.3em;
    line-height: 1.5em;
}

.container {
    max-width: 640px;
    margin: 50px auto;
    background: #fff;
    padding: 20px;
    border-radius: 10px;
}

form input, form label, form button {
    display: block;
    width: 100%;
    margin-bottom: 10px;
}

form input {
    font-size: 24px;
    height: 50px;
    padding: 0 20px;
}

form input:focus {
    outline: 1px solid var(--primary-color);
}

form button {
    border: none;
    background: var(--primary-color);
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    height: 50px;
    cursor: pointer;
    margin-top: 30px;
}

form button:hover {
    background: var(--primary-color-darker);
}

form button:active {
    background: blue;
}

.informacoes {
    display: grid;
    grid-template-columns: repeat(2, 300px);
}

.calculo {
    display: grid;
    grid-template-columns: 1;
}

.resultado-top {
    background: var(--sucesso);
    padding: 10px 20px;
}

.resultado-ruim {
    background: var(--falha);
    padding: 10px 20px;
}

.container h1 {
    text-align: center;
    margin-bottom: 40px;
}

.cpf-gerado{
    font-size: 2em;
    color: var(--primary-color);
    text-align: center;
    font-weight: bold;
    margin-bottom: 40px;
    letter-spacing: 3px;
